*{
  margin: 0px;
  padding: 0px;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}




body {
    margin: 0;
    padding: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    height: 100vh;
    background-image: url(images/bg.jpg);
    overflow-x: hidden;
    background-position: center;
background-size: cover;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}
.main{
  text-align: center;
  padding-top: 12%;
  max-width: 70%;
margin: 0 auto;
}
.main h1 {
    font-size: 3.5em;
    font-weight: 900;
    color: #fae219;
    text-transform: uppercase;
}
.main h3{
    font-size: 2.2em;
    font-weight: 100;
    color: #fff;
}
span.sticky {
    color: #aba67e;
    font-size: 1.6em;
    position: fixed;
    bottom: 5%;
    left: 0;
    right: 0px;
    text-align: center;
    font-weight: 100;
}
.main ul{
  margin-top: 100px;
}
.main ul li {
    display: inline-block;
    color: #fff;
    text-transform: uppercase;
    padding: 10px 20px;
    font-size: 1.3em;
}
.nav {
    text-align: center;
    position: relative;
    top: 40px;
    margin: 0 auto;
}
.nav li {
    display: inline-block;
    margin: 0 20px;
    font-size: 18px;
    text-transform: uppercase;
}
.nav li a {
    color: #fff;
}
.nav li a.selected {
    font-weight: 600;
    color: #ffe302;
}

.holder {
    max-width: 80%;
    margin: 0 auto;
    position: relative;
    top: 110px;
}
.holder li {
    color: #fff;
    font-size: 22px;
    font-weight: 100;
    margin-bottom: 5px;
}

.block {
    background-color: #020204;
    padding: 20px 25px;
    margin: 20px 0;
}
.holder li span {
    font-weight: 600;
    color: #ffe302;
    font-size: 18px;
}
.holder li{
  font-size: 16px;
}
.col {
    width: 48%;
    display: inline-block;
    margin: 10px 0;
}
